.loginBox {
  display: flex;
  justify-content: center;
  flex-direction: column;
  .loginForm {
    color: rgba(0, 0, 0, 0.8);
    border: 1px solid grey;
    border-radius: 5px;
    margin: 20px auto;
    max-width: 350px;
    min-width: 280px;
    padding: 10px;
    background-color: rgba(245, 245, 245, 0.6);
    z-index: 2;
    position: relative;
    & form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      & .label {
        font-weight: 700;
      }
      & input {
        margin: 5px 0;
        border: 1px solid grey;
        border-radius: 4px;
        height: 30px;
        padding-left: 10px;
        &:hover {
          box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.7);
        }
      }
      & button {
        width: 200px;
        margin: 0 auto;
        margin-top: 20px;
        height: 30px;
        border: 1px solid grey;
        border-radius: 4px;
        background-color: white;
        cursor: pointer;
        &:hover {
          background-color: #6c63ff;
          color: whitesmoke;
        }
      }
    }
  }
  & .loginSvg {
    max-width: 100vw;
    margin-top: -220px;
  }
}
